import { sudokuHelpers } from "@/helpers";

const highlight = {
  namespaced: true,
  state: () => ({
    selectedDigitIndexes: [],
    selectedRowIndexes: [],
    selectedColumnIndexes: [],
    selectedBoxIndexes: [],
  }),
  mutations: {
    setSelectedDigitIndexesMutation(state, index) {
      state.selectedDigitIndexes = index.sort();
    },
    setSelectedRowIndexesMutation(state, rowIndexes) {
      state.selectedRowIndexes = rowIndexes;
    },
    setSelectedColumnIndexesMutation(state, columnIndexes) {
      state.selectedColumnIndexes = columnIndexes;
    },
    setSelectedBoxIndexesMutation(state, boxIndexes) {
      state.selectedBoxIndexes = boxIndexes;
    },
  },
  actions: {
    updateIndexes(context, index) {
      const ROW_SIZE = context.rootState.root.ROW_SIZE;
      const COL_SIZE = context.rootState.root.COL_SIZE;
      const BOX_SIZE = context.rootState.root.BOX_SIZE;
      const currentSudokuString = context.rootState.sudokuStrings.current;
      context.commit(
        "setSelectedRowIndexesMutation",
        sudokuHelpers.getRowIndexes(index, ROW_SIZE)
      );
      context.commit(
        "setSelectedColumnIndexesMutation",
        sudokuHelpers.getColIndexes(index, ROW_SIZE, COL_SIZE)
      );
      context.commit(
        "setSelectedBoxIndexesMutation",
        sudokuHelpers.getBoxIndexes(index, ROW_SIZE, BOX_SIZE)
      );
      context.commit(
        "setSelectedDigitIndexesMutation",
        sudokuHelpers.getSameDigitIndexes(index, currentSudokuString)
      );
    },
  },
  getters: {
    selectedRowIndexes: (state) => state.selectedRowIndexes,
    selectedColumnIndexes: (state) => state.selectedColumnIndexes,
    selectedBoxIndexes: (state) => state.selectedBoxIndexes,
    selectedDigitIndexes: (state) => state.selectedDigitIndexes,
  },
};

export default highlight;
