let sudokuHelpers = {
  /**
   * Returns the index of the row starting from 1
   * @param {number} index
   * @param {number} ROW_SIZE
   * @returns {number}
   */
  getRow(index, ROW_SIZE) {
    return Math.ceil(index / ROW_SIZE);
  },

  /**
   * Returns the index of the column starting from 1
   * @param {number} index
   * @param {number} ROW_SIZE
   * @returns {number}
   */
  getColumn(index, ROW_SIZE) {
    return index % ROW_SIZE || ROW_SIZE;
  },

  /**
   * Returns the index of the box starting from 1
   * @param {number} index
   * @param {number} ROW_SIZE
   * @param {number} BOX_SIZE
   * @returns {number}
   */
  getBox(index, ROW_SIZE, BOX_SIZE) {
    // calculate and set selected box
    let boxesPerRow = ROW_SIZE / BOX_SIZE;
    let currentBoxRow = this.getBoxRow(index, ROW_SIZE, BOX_SIZE);
    let currentBoxCol = this.getBoxCol(index, ROW_SIZE, BOX_SIZE);
    return (currentBoxRow - 1) * boxesPerRow + currentBoxCol;
  },

  /**
   * Returns the box row index starting from 1. On a 9x9 sudoku grid the 4th box will be in row 2
   * @param {number} index
   * @param {number} ROW_SIZE
   * @param {number} BOX_SIZE
   * @returns {number}
   */
  getBoxRow(index, ROW_SIZE, BOX_SIZE) {
    return Math.ceil(this.getRow(index, ROW_SIZE) / BOX_SIZE);
  },

  /**
   * Returns the box column index starting from 1. On a 9x9 sudoku grid the 5th box will be in column 2
   * @param {number} index
   * @param {number} ROW_SIZE
   * @param {number} BOX_SIZE
   * @returns {number}
   */
  getBoxCol(index, ROW_SIZE, BOX_SIZE) {
    return Math.ceil(this.getColumn(index, ROW_SIZE) / BOX_SIZE);
  },

  /**
   * Returns the indexes of the cells in the same row as the specified index.
   * The returned indexes are not 0 based and start from 1
   * @param {Number} index
   * @param {Number} ROW_SIZE
   * @returns {Array.<Number>}
   */
  getRowIndexes(index, ROW_SIZE) {
    let row = this.getRow(index, ROW_SIZE);
    let indexes = [];
    for (let i = 0; i < ROW_SIZE; i++) {
      indexes.push((row - 1) * ROW_SIZE + i + 1);
    }
    return indexes;
  },

  /**
   * Returns the indexes of the cells in the same column as the specified index.
   * The returned indexes are not 0 based and start from 1
   * @param {Number} index
   * @param {Number} ROW_SIZE
   * @returns {Array.<Number>}
   */
  getColIndexes(index, ROW_SIZE, COL_SIZE) {
    let col = this.getColumn(index, ROW_SIZE);
    let indexes = [];
    for (let i = 0; i < COL_SIZE; i++) {
      indexes.push(col + i * ROW_SIZE);
    }
    return indexes;
  },

  /**
   * Returns the indexes of the cells in the same box as the specified index.
   * The returned indexes are not 0 based and start from 1
   * @param {Number} index
   * @param {Number} ROW_SIZE
   * @returns {Array.<Number>}
   */
  getBoxIndexes(index, ROW_SIZE, BOX_SIZE) {
    let indexes = [];

    let boxRow = this.getBoxRow(index, ROW_SIZE, BOX_SIZE);
    let boxCol = this.getBoxCol(index, ROW_SIZE, BOX_SIZE);

    // the very first cell in the box. index is not 0 based hense the +1
    let boxRootIndex =
      (boxRow - 1) * BOX_SIZE * ROW_SIZE + (boxCol - 1) * BOX_SIZE + 1;

    for (let i = 0; i < BOX_SIZE; i++) {
      // loop rows of the box
      for (let j = 0; j < BOX_SIZE; j++) {
        // loop the columns in the box for the row
        indexes.push(boxRootIndex + i + j * ROW_SIZE);
      }
    }
    return indexes;
  },

  /**
   * Returns the indexes of the cells with the same digit as the digit in the cell with the specified index
   * The returned indexes are not 0 based and start from 1
   * @param {Number} index
   * @param {Number} ROW_SIZE
   * @returns {Array.<Number>}
   */
  getSameDigitIndexes(index, string) {
    if (string[index - 1] === ".") return [];
    var indexes = [];
    for (var i = 1; i <= string.length; i++) {
      if (string[i - 1] === string[index - 1]) indexes.push(i);
    }
    return indexes;
  },

  /**
   * Validates if the provided string is a valid solution for a sudoku puzzle
   * Checks all rows, columns and boxes to have the digits from 1 to 9 without any repeats
   * @param {String} sudokuSring
   * @returns {Boolean}
   */
  validateSudokuString(sudokuSring) {
    var rows = [];
    var cols = [];
    var cells = [];

    let column, row, cell, value;

    for (let index = 0; index < sudokuSring.length; index++) {
      column = (index + 9) % 9; // returns the idex of the colum starting from 0
      row = Math.floor(index / 9); // returns the idex of the row starting from 0
      cell = (Math.floor(index / 3) % 3) + 3 * Math.floor(index / 27); // cell index starting from 0

      rows[row] = rows[row] || []; // initiate the array if needed
      cols[column] = cols[column] || []; // initiate the array if needed
      cells[cell] = cells[cell] || []; // initiate the array if needed

      value = sudokuSring.charAt(index);
      rows[row][column] = value; // store the value
      cols[column][row] = value; // store the value
      cells[cell].push(value);
    }

    var valid = true;
    function checkArray(arr) {
      if (arr.sort().join("") != "123456789") {
        valid = false;
      }
    }

    rows.forEach(checkArray);
    cols.forEach(checkArray);
    cells.forEach(checkArray);
    return valid;
  },
};

export { sudokuHelpers };
