import { sudokuHelpers } from "@/helpers";

let mutations = {
  setBackroundMutation(state, backgroundShown) {
    state.backgroundShown = backgroundShown;
  },
  setMousedownMutation(state, mousedown) {
    state.mousedown = mousedown;
  },
  setAddingSelectionMutation(state, selection) {
    state.addingSelection = selection;
  },
  setSelectedIndexesMutation(state, index) {
    state.selectedIndexes = index;
  },
  setActiveIndexMutation(state, index) {
    state.activeIndex = index;
  },
};

let actions = {
  showBackgroundAction: function (context) {
    context.commit("setBackroundMutation", true);
  },
  hideBackgroundAction: function (context) {
    context.commit("setBackroundMutation", false);
  },
  setMousedownAction: function (context, mousedown) {
    context.commit("setMousedownMutation", mousedown);
  },
  setAddingSelectionAction: function (context, selection) {
    context.commit("setAddingSelectionMutation", selection);
  },
  setSelectedIndexesAction: function (context, index) {
    if (typeof index === "number") {
      context.dispatch("setActiveIndexAction", index); // only set the active index for a number e.g. click without ctrl
      index = [index];
    }
    context.commit("setSelectedIndexesMutation", index);
  },
  setActiveIndexAction: function (context, index) {
    context.commit("setActiveIndexMutation", index);
  },
  updateSelectedIndexesAction: function (context, index) {
    if (!context.state.mousedown)
      context.dispatch("setActiveIndexAction", index); // keep the active index the last clicked cell
    let newIndexes = [...context.state.selectedIndexes] || []; // init the indexes array

    if (context.state.addingSelection) {
      newIndexes.push(index);
    } else {
      let location = newIndexes.indexOf(index);
      if (location !== -1) {
        // if not adding e.g. removing from the selection and it is already selected
        newIndexes.splice(location, 1);
      }
    }
    newIndexes = [...new Set(newIndexes)]; // filter to unique values in the array
    context.dispatch("setSelectedIndexesAction", newIndexes);
  },

  moveLeft: function (context) {
    // default is 2 since we'll decrease it
    let index = context.state.activeIndex;

    // if we are on the first element on a row - move to the end
    if (index % context.state.ROW_SIZE == 1) {
      index += context.state.ROW_SIZE;
    }

    // decrease the index
    index--;
    context.dispatch("setSelectedIndexesAction", index);
  },
  moveRight: function (context) {
    let index = context.state.activeIndex;

    // if we are on the last element on a row - move to the beggining
    if (index % context.state.ROW_SIZE == 0) {
      index -= context.state.ROW_SIZE;
    }

    // decrease the index
    index++;
    context.dispatch("setSelectedIndexesAction", index);
  },
  moveUp: function (context) {
    let index = context.state.activeIndex;

    // decrese the column
    index -= context.state.ROW_SIZE;

    // if we are on the last element on a column - move to the top
    let row = sudokuHelpers.getRow(index, context.state.ROW_SIZE);
    if (row < 1) {
      index = index + context.state.ROW_SIZE * context.state.COL_SIZE;
    }
    context.dispatch("setSelectedIndexesAction", index);
  },
  moveDown: function (context) {
    let index = context.state.activeIndex;

    // increase the column
    index += context.state.ROW_SIZE;

    // if we are on the last element on a column - move to the top
    let row = Math.ceil(index / context.state.COL_SIZE);
    if (row > context.state.COL_SIZE) {
      index = index % context.state.ROW_SIZE;
    }
    context.dispatch("setSelectedIndexesAction", index);
  },
};

let getters = {
  ROW_SIZE: (state) => state.ROW_SIZE,
  COL_SIZE: (state) => state.COL_SIZE,
  BOX_SIZE: (state) => state.BOX_SIZE,
  backgroundShown: (state) => state.backgroundShown,
  mousedown: (state) => state.mousedown,
  selectedIndexes: (state) => state.selectedIndexes,
  activeIndex: (state) => state.activeIndex,
};

const state = {
  namespaced: true,
  state: {
    ROW_SIZE: 9,
    COL_SIZE: 9,
    BOX_SIZE: 3,
    backgroundShown: false,
    mousedown: false,
    addingSelection: true,
    selectedIndexes: [],
    activeIndex: 0,
  },
  mutations,
  actions,
  getters,
};

export default state;
