import Vue from "vue";
import Vuex from "vuex";

import root from "./root";
import settings from "./settings";
import controls from "./controls";
import sudokuStrings from "./sudokuStrings";
import clues from "./clues";
import highlight from "./highlight";
import { constants } from "../constants";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    root,
    settings,
    controls,
    sudokuStrings,
    clues,
    highlight,
  },
  mutations: {
    initialiseStoreMutation(state, storedState) {
      this.replaceState(Object.assign(state, storedState));
    },
  },
  actions: {
    initialiseStoreAction: function (context) {
      // Check if the ID exists
      let storedState = JSON.parse(
        localStorage.getItem(constants.LOCAL_STORAGE_KEY)
      );

      // clear old and obsolete states
      let version = localStorage.getItem(constants.LOCAL_STORAGE_VERSION_KEY);

      if (version != constants.VERSION) {
        storedState = null;
        localStorage.setItem(
          constants.LOCAL_STORAGE_VERSION_KEY,
          constants.VERSION
        );
      }

      // storedState = null;
      if (storedState && storedState.sudokuStrings.initial) {
        // Replace the state object with the stored item
        context.commit("initialiseStoreMutation", storedState);
      } else {
        let str =
          ".1..8..7.8...3...9...9.1.....3...6..94.....53..8...9.....5.6...7...1...2.5..2..1.";
        context.dispatch("sudokuStrings/setInitialAction", str);
      }
    },
    undo(context) {
      // check if we have actions for undo
      if (doneActions.length === 0) return;

      // pop the last action from the array
      let lastAction = doneActions.pop();

      // add it to the array with actions that have been udone
      undoneActions.push(lastAction);
      // reset the store to the initial state
      let stateToUse = JSON.parse(JSON.stringify(initialState)); // the state is mutated somewhere within the dispatch process
      context.commit("initialiseStoreMutation", stateToUse);
      // do all actions again to get the the second to last state
      replayingActions = true;
      doneActions.forEach((action) => {
        if (blacklistReplayAction.indexOf(action.type) !== -1) return false; // don't replay blacklisted actions
        switch (typeof action.payload) {
          case "object":
            context.dispatch(
              action.type,
              JSON.parse(JSON.stringify(action.payload))
            );
            break;
          default:
            context.dispatch(action.type, action.payload);
        }
      });
      replayingActions = false;
    },

    redo(context) {
      // check if we have actions for undone actions
      if (undoneActions.length === 0) return;

      // pop the last action from the array
      let lastAction = undoneActions.pop();

      redoingAction = true; // prevend the clear of the undone actions
      context.dispatch(lastAction.type, lastAction.payload); // dispatch the last undone action again
      redoingAction = false;
    },
  },
});

let replayingActions = false;
let redoingAction = false;
let initialState = null;
let doneActions = [];
let undoneActions = [];
let whitelistedActions = [
  "sudokuStrings/setInitialAction",
  "sudokuStrings/setGuessAction",
  "sudokuStrings/clearGuessAction",
  "clues/toggleClueAction",
  "clues/clearCluesAction",
];

let blacklistReplayAction = ["root/showBackgroundAction"];

store.subscribeAction((action, state) => {
  if (whitelistedActions.indexOf(action.type) !== -1 && !replayingActions) {
    if (initialState === null) {
      initialState = JSON.parse(JSON.stringify(state));
    }
    doneActions.push(action);
    if (!redoingAction) undoneActions = []; // reset the redo stack if this is not a redo
  }
});

store.subscribe((_mutation, state) => {
  // Store the state object as a JSON string
  localStorage.setItem(constants.LOCAL_STORAGE_KEY, JSON.stringify(state));

  switch (_mutation.type) {
    case "root/setActiveIndexMutation":
      store.dispatch("highlight/updateIndexes", _mutation.payload);
      break;
  }
});

export { store };

