import { constants } from "@/constants";

let mutations = {
  toggleClueMutation(state, { index, type, clue }) {
    // we are getting a cell index which is not 0 based but 1 based
    index--;
    let clues = [...state[type][index]];
    const clueIndex = clues.indexOf(clue);
    if (clueIndex > -1) {
      clues.splice(clueIndex, 1);
    } else {
      clues.push(clue);
    }
    clues.sort();
    state[type].splice(index, 1, clues);
  },
  clearCluesMutation(state, { index, type }) {
    // we are getting a cell index which is not 0 based but 1 based
    index--;
    let clues = [];
    state[type].splice(index, 1, clues);
  },
};

let actions = {
  toggleClueAction(context, { selectedCellsIndexes, type, clue }) {
    selectedCellsIndexes.forEach((index) => {
      context.commit("toggleClueMutation", { index, type, clue });
    });
  },
  clearCluesAction(context, selectedCellsIndexes) {
    // detect if we hacve corner clues and clear those. Otherwise clear the center clues
    let hasCornerClues = false;
    selectedCellsIndexes.forEach((index) => {
      let clues = [...context.state[constants.INPUT_METHODS.CORNER][index - 1]];
      if (clues.length > 0) {
        hasCornerClues = true;
      }
    });
    let type = hasCornerClues
      ? constants.INPUT_METHODS.CORNER
      : constants.INPUT_METHODS.CENTER;

    selectedCellsIndexes.forEach((index) => {
      context.commit("clearCluesMutation", { index, type });
    });
  },
  clearAllClues(context) {
    // call it twice to clear both types of clues
    context.dispatch(
      "clearCluesAction",
      Array.from({ length: 81 }, (_, i) => i + 1)
    );
    context.dispatch(
      "clearCluesAction",
      Array.from({ length: 81 }, (_, i) => i + 1)
    );
  },
};

let getters = {
  getCluesOfType(state) {
    return function (index, type) {
      index--;
      return [...state[type][index]];
    };
  },
};

const clues = {
  namespaced: true,
  state: {
    corner: new Array(81).fill(new Array()),
    center: new Array(81).fill(new Array()),
  },
  mutations,
  actions,
  getters,
};

export default clues;
