/**
 * Replaces the character at the specified index with the specified character
 * @param {String} str
 * @param {Number} index
 * @param {String} chr
 * @returns {String}
 */
function setCharAt(str, index, chr) {
  if (index > str.length - 1) return str;
  return str.substring(0, index) + chr + str.substring(index + 1);
}

export { setCharAt };
