<template>
  <div class="settings">
    <p>Settings are automatically saved.</p>
    <p>
      <input type="checkbox" id="highlightRCB" v-model="hlRCB" />
      <label for="highlightRCB">Auto highlight row, column and box</label>
    </p>

    <p>
      <input type="checkbox" id="highlightDigits" v-model="hlDigits" />
      <label for="highlightDigits">Auto highlight same digits</label>
    </p>

    <p><button class="primary" @click="close()">Close</button></p>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Settings",
  data: function () {
    return { highlightRCB: false, highlightDigits: false };
  },
  computed: {
    hlRCB: {
      get: function () {
        return this.$store.state.settings.hlRCB;
      },
      set: function (val) {
        this.setHlRCBAction(val);
      },
    },
    hlDigits: {
      get: function () {
        return this.$store.state.settings.hlDigits;
      },
      set: function (val) {
        this.setHlDigitsAction(val);
      },
    },
  },
  methods: {
    ...mapActions("settings", ["setHlRCBAction", "setHlDigitsAction"]),
    ...mapActions("root", ["hideBackgroundAction"]),
    close() {
      this.hideBackgroundAction();
      this.$emit("close");
    },
  },
};
</script>

