.settings {
  float: left;
  text-align: left;
  position: fixed;
  top: 110px;
  left: -webkit-calc(50% - 645px/2);
  left: -moz-calc(50% - 645px/2);
  left: calc(50% - 645px / 2);
  width: 645px;
  font-weight: normal;
  font-size: 20px;
  background-color: white;
  padding: 40px;
  border: 2px solid #750b95;
  border-radius: 10px;
