<template>
  <div class="inputString">
    <p>Start a new gamme by entering the sudoku string here.</p>
    <p>
      <label for="inputString">Auto highlight row, column and box</label><br />
      <input type="text" id="inputString" v-model="inputString" />
    </p>
    <p v-if="showError" class="error">
      String must be 81 characters long, containing only 1-9 and '.'!
    </p>
    <p>
      <button class="primary" @click="importString()">Import</button>
      <button @click="close()">Close</button>
    </p>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "InputString",
  data: function () {
    return { inputString: "", showError: false };
  },
  computed: {},
  methods: {
    ...mapActions("sudokuStrings", ["importGridAction"]),
    ...mapActions("root", ["hideBackgroundAction"]),
    ...mapActions("clues", ["clearAllClues"]),
    importString() {
      let regex = new RegExp("[1-9.]{81}");
      if (regex.test(this.inputString)) {
        this.importGridAction(this.inputString);
        this.clearAllClues();
        this.close();
      } else {
        this.showError = true;
      }
    },
    close() {
      this.showError = false;
      this.hideBackgroundAction();
      this.$emit("close");
    },
  },
};
</script>

<style lang="scss" scoped>
$windowWidth: 750px;
button {
  margin: 0px 30px 0px 0px;
}
.inputString {
  float: left;
  text-align: left;
  position: fixed;
  top: 110px;
  left: -webkit-calc(50% - #{$windowWidth}/ 2);
  left: -moz-calc(50% - #{$windowWidth}/ 2);
  left: calc(50% - #{$windowWidth} / 2);
  width: #{$windowWidth};
  font-weight: normal;
  font-size: 20px;
  background-color: white;
  padding: 40px;
  border: 2px solid #750b95;
  border-radius: 10px;
  z-index: 2;
}
.inputString input {
  font-size: 16px;
  margin-top: 10px;
  color: #750b95;
  width: 650px;
  border: 2px solid #750b95;
  border-radius: 4px;
  padding: 5px;
}
.inputString p {
  padding: 10px;
}

.error {
  color: rgb(209, 59, 59);
}
</style>
