<template>
  <ul>
    <Cell
      :value="current.charAt(index - 1)"
      v-for="index in ROW_SIZE * COL_SIZE"
      :index="index"
      :key="index"
      :initialValue="initial.charAt(index - 1)"
    />
  </ul>
</template>

<script>
import { mapGetters } from "vuex";
import Cell from "./Cell.vue";
export default {
  components: { Cell },
  name: "Grid",
  props: {
    msg: String,
  },
  computed: {
    ...mapGetters("root", ["ROW_SIZE", "COL_SIZE"]),
    ...mapGetters("sudokuStrings", ["current", "initial"]),
  },
};
</script>

<style lang="scss" scoped>
$selectedBackground: #f3f189;
$hoverBackground: #fcd889;
$sameDigitBackground: #eeeab5;
$sameRowBackground: #dee7ff;
$sameColumnBackground: #dee7ff;
$sameBoxBackground: #e9f0e0;

$gridSize: 800px;
$numberOfRows: 9;
$numberOfColumns: 9;
$boxsize: 3;
$numberOfRowsScope: $numberOfRows - 1;
$totalBoxes: ($numberOfRows * $numberOfColumns) / ($boxsize * $boxsize); // total cells devided by the number of cells per box
$totalBoxRows: $numberOfColumns / $boxsize;

ul {
  list-style: none;
  width: $gridSize;
  height: $gridSize;
  float: left;
  background-color: #fff;
  position: absolute;
  top: 60px;
  left: -webkit-calc(50% - #{$gridSize}/ 2);
  left: -moz-calc(50% - #{$gridSize}/ 2);
  left: calc(50% - #{$gridSize} / 2);

  top: -webkit-calc(50% - #{$gridSize}/ 2);
  top: -moz-calc(50% - #{$gridSize}/ 2);
  top: calc(50% - #{$gridSize} / 2);
}

li {
  float: left;
  width: percentage(1 / $numberOfRows);
  height: percentage(1 / $numberOfRows);
  border: 1px solid #555;
  border-bottom: none;
  border-right: none;
  text-align: center;
  font-size: $gridSize/15;
}

/*
  ROW-SIZE = 6
  COL-SIZE = 6
  BOX-SIZE = 3
 */

$boxIndexSize: $numberOfRows * $boxsize;
$boxIndex1: (1-1) * $boxIndexSize + 1; // 1st line of boxes
$boxIndex1end: $boxIndex1 + $numberOfRows - 1;

$boxIndex2: (2-1) * $boxIndexSize + 1; // 2nd line of boxes
$boxIndex2end: $boxIndex2 + $numberOfRows - 1;

$boxIndex3: (3-1) * $boxIndexSize + 1; // 3rd line of boxes
$boxIndex3end: $boxIndex3 + $numberOfRows - 1;

$boxIndex4: (4-1) * $boxIndexSize + 1; // 4th line of boxes
$boxIndex4end: $boxIndex4 + $numberOfRows - 1;

$boxIndex5: (5-1) * $boxIndexSize + 1; // 5th line of boxes
$boxIndex5end: $boxIndex5 + $numberOfRows - 1;

$boxIndexLast: ($totalBoxRows * $boxsize - 1) * $numberOfRows + 1; //last line of boxes
$boxIndexLastEnd: $boxIndex5 + $numberOfRows - 1; // last line of boxes

li:nth-child(#{$boxsize}n) {
  border-right: 4px solid #555;
}

li:nth-child(#{$numberOfRows}n-#{$numberOfRowsScope}) {
  border-left: 4px solid #555;
}
li:nth-child(n + #{$boxIndex5}):nth-child(-n + #{$boxIndex5end}),
li:nth-child(n + #{$boxIndex4}):nth-child(-n + #{$boxIndex4end}),
li:nth-child(n + #{$boxIndex3}):nth-child(-n + #{$boxIndex3end}),
li:nth-child(n + #{$boxIndex2}):nth-child(-n + #{$boxIndex2end}),
li:nth-child(n + #{$boxIndex1}):nth-child(-n + #{$boxIndex1end}) {
  border-top: 4px solid #555;
}

//.sudoku li:nth-child(n+73):nth-child(-n+81) {
li:nth-child(n + #{$boxIndexLast}):nth-child(-n + #{$boxIndexLastEnd}) {
  border-bottom: 4px solid #555;
}

.active {
  border: 6px dashed rgb(146, 207, 247) !important;
}

.selected {
  background-color: $selectedBackground !important;
}
li:hover {
  background-color: $hoverBackground !important;
}
li.sameBox {
  background-color: $sameBoxBackground;
}
li.sameRow {
  background-color: $sameRowBackground;
}
li.sameColumn {
  background-color: $sameColumnBackground;
}
li.sameDigit {
  background-color: $sameDigitBackground;
}

li.customDigit {
  color: rgb(0, 89, 255);
}
</style>
