let mutations = {
  setHlRCBMutation(state, hlRCB) {
    state.hlRCB = hlRCB;
  },
  setHlDigitsMutation(state, hlDigits) {
    state.hlDigits = hlDigits;
  },
};

let actions = {
  setHlRCBAction(context, hlRCB) {
    context.commit("setHlRCBMutation", hlRCB);
  },

  setHlDigitsAction(context, hlDigits) {
    context.commit("setHlDigitsMutation", hlDigits);
  },
};

let getters = {
  hlRCB: (state) => state.hlRCB,
  hlDigits: (state) => state.hlDigits,
};

const settings = {
  namespaced: true,
  state: {
    hlRCB: true,
    hlDigits: true,
  },
  mutations,
  actions,
  getters,
};

export default settings;
