<template>
  <div class="centerclues">
    <div class="holder">
      {{ val }}
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "CenterClues",
  computed: {
    val() {
      return this.clues.join("");
    },
    clues() {
      let clues = this.$store.getters["clues/getCluesOfType"](
        this.$parent.index,
        "center"
      );
      return clues;
    },
  },
};
</script>
<style lang="scss" scoped>
$fontSize: 20px;
.centerclues {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.holder {
  position: absolute;
  left: 0;
  top: calc(50% - #{$fontSize/2});
  width: 100%;
  height: $fontSize;
  font-size: $fontSize;
  line-height: $fontSize;
  color: blue;
}
</style>
