<template>
  <div class="cornerlclues">
    <span v-for="clue in clues" :key="clue">{{ clue }}</span>
  </div>
</template>

<script>
export default {
  name: "CornerClues",
  computed: {
    clues() {
      let clues = this.$store.getters["clues/getCluesOfType"](
        this.$parent.index,
        "corner"
      );
      return clues;
    },
  },
};
</script>
