import { setCharAt } from "@/helpers";

let mutations = {
  setInitialMutation(state, string) {
    state.initial = string;
  },
  setCurrentMutation(state, string) {
    state.current = string;
  },
  setGeussesMutation(state, string) {
    state.guesses = string;
  },
};

let actions = {
  setInitialAction(context, string) {
    context.commit("setInitialMutation", string);
    context.commit("setCurrentMutation", string);
  },
  setCurrentAction(context, string) {
    context.commit("setCurrentMutation", string);
  },
  setGeussesAction(context, string) {
    context.commit("setGeussesMutation", string);
  },
  // guesses
  setGuessAction(context, { indexes, digit = "." }) {
    let curentString = context.state.current;
    let guessesString = context.state.guesses;
    indexes.forEach((index) => {
      // we should not change the value of the initial clues
      if (context.state.initial.charAt(index - 1) == ".") {
        curentString = setCharAt(curentString, index - 1, digit);
        guessesString = setCharAt(guessesString, index - 1, digit);
      }
    });
    context.dispatch("setCurrentAction", curentString);
    context.dispatch("setGeussesAction", guessesString);
  },
  clearGuessAction: function (context, indexes) {
    context.dispatch("setGuessAction", { indexes });
  },
  //grid
  resetGridAction: function (context) {
    context.dispatch("setCurrentAction", context.state.initial);
  },
  newGridAction: function (context) {
    context.dispatch("setInitialAction", new Array(81).fill(".").join(""));
  },
  importGridAction: function (context, string) {
    context.dispatch("setInitialAction", string);
  },
};

let getters = {
  current: (state) => state.current,
  initial: (state) => state.initial,
  guesses: (state) => state.guesses,
};

const state = {
  namespaced: true,
  state: {
    current: new Array(81).fill(".").join(""),
    initial: new Array(81).fill(".").join(""),
    guesses: new Array(81).fill(".").join(""),
  },
  mutations,
  actions,
  getters,
};

export default state;
