<template>
  <div id="app" class="">
    <h1>Sudoku</h1>
    <Grid ref="Grid" />
    <div class="input-methods">
      <button
        :class="inputMethod == constants.INPUT_METHODS.CORNER ? 'selected' : ''"
        @click="setInputMethodAction(constants.INPUT_METHODS.CORNER)"
      >
        Corner
      </button>
      <br />
      <button
        :class="inputMethod == constants.INPUT_METHODS.CENTER ? 'selected' : ''"
        @click="setInputMethodAction(constants.INPUT_METHODS.CENTER)"
      >
        Center
      </button>
      <br />
      <button
        :class="inputMethod == constants.INPUT_METHODS.VALUE ? 'selected' : ''"
        @click="setInputMethodAction(constants.INPUT_METHODS.VALUE)"
      >
        Value
      </button>
    </div>
    <Controls />
    <div
      class="background"
      v-show="backgroundShown"
      @click="hideBackgroundAction()"
    ></div>
    <div id="footer">
      Source and Issues:
      <a href="https://github.com/alexanderstoev/sudoku" target="_blank"
        >GitHub Repo</a
      >
      v{{ appVersion }}
    </div>
  </div>
</template>

<script>
import { constants } from "@/constants";

import { mapActions, mapGetters } from "vuex";
import Grid from "./components/Grid.vue";
import Controls from "./components/UI/Controls.vue";

export default {
  name: "App",
  components: {
    Grid,
    Controls,
  },
  created() {
    this.constants = constants;
  },
  methods: {
    ...mapActions("controls", [
      "setInputMethodAction",
      "toggleInputMethodAction",
    ]),
    ...mapActions("sudokuStrings", ["setGuessAction", "clearGuessAction"]),
    ...mapActions("clues", ["clearCluesAction", "toggleClueAction"]),
    ...mapActions("root", ["moveLeft", "moveRight", "moveUp", "moveDown"]),
    ...mapActions("root", ["hideBackgroundAction", "setMousedownAction"]),
    handleKeyPress: function (evt) {
      switch (evt.keyCode) {
        case 37: //ArrowLeft
          this.moveLeft();
          evt.preventDefault();
          break;

        case 39: //ArrowRight
          this.moveRight();
          evt.preventDefault();
          break;

        case 38: //ArrowUp
          this.moveUp();
          evt.preventDefault();
          break;

        case 40: //ArrowDown
          this.moveDown();
          evt.preventDefault();
          break;

        case 32: // spacebar
          this.toggleInputMethodAction();
          evt.preventDefault();
          break;

        case 8: // Backspace
        case 46: // Delete
          this.handleClearCell();
          evt.preventDefault();
          break;
      }

      // digits
      if (
        this.inputMethod == constants.INPUT_METHODS.VALUE &&
        !isNaN(parseInt(evt.key))
      ) {
        let indexes = this.$store.state.root.selectedIndexes;
        let digit = evt.key;
        this.setGuessAction({ indexes, digit });
        return;
      }
      // more digits but for clues
      if (!isNaN(parseInt(evt.key))) {
        let selectedCellsIndexes = this.$store.state.root.selectedIndexes;
        let clue = evt.key;
        let type = this.inputMethod;
        this.toggleClueAction({ selectedCellsIndexes, type, clue });
        return;
      }
    },

    handleClearCell() {
      let selectedCellsIndexes = this.$store.state.root.selectedIndexes;

      // first clear all values
      let hasValue = false;
      selectedCellsIndexes.forEach((index) => {
        if (this.guesses.charAt(index - 1) != ".") {
          hasValue = true;
        }
      });
      if (hasValue) {
        this.clearGuessAction(selectedCellsIndexes);
        return; // stop after clearing at least one value
      }
      this.clearCluesAction(selectedCellsIndexes);
    },

    handleMouseUp: function () {
      this.setMousedownAction(false);
    },
  },
  computed: {
    ...mapGetters("controls", ["inputMethod"]),
    ...mapGetters("root", ["backgroundShown"]),
    ...mapGetters("sudokuStrings", ["guesses"]),
    appVersion: () => constants['VERSION'],
  },
  mounted() {
    let that = this;
    document.addEventListener("keydown", function (evt) {
      if (!that.$store.state.root.backgroundShown) that.handleKeyPress(evt);
    });
    document.addEventListener("mouseup", function () {
      if (!that.$store.state.root.backgroundShown) that.handleMouseUp();
    });
  },
};
</script>

