  left: -webkit-calc(50% + 800px / 2 + 150px);
  left: -moz-calc(50% + 800px / 2 + 150px);
  left: calc(50% + 800px / 2 + 150px);

  top: -webkit-calc(50% - 800px/2);
  top: -moz-calc(50% - 800px/2);
  top: calc(50% - 800px / 2);
}

