<template>
  <div class="controls">
    <button @click="checkSudoku()" class="icon">
      <span class="material-icons"> done </span>
    </button>
    <br />
    <button @click="resetGame()" class="icon">
      <span class="material-icons"> sync </span>
    </button>
    <br />
    <button @click="newGame()" class="icon">
      <span class="material-icons"> fiber_new </span>
    </button>
    <br />
    <button
      @click="toggleInput()"
      class="icon"
      :class="inputShown ? 'selected' : ''"
    >
      <span class="material-icons"> keyboard </span>
    </button>
    <br />
    <button
      @click="toggleSettings()"
      class="icon"
      :class="settingsShown ? 'selected' : ''"
    >
      <span class="material-icons"> settings </span>
    </button>
    <br />
    <button
      @click="toggleHelp()"
      class="icon"
      :class="helpShown ? 'selected' : ''"
    >
      <span class="material-icons"> help_outline </span>
    </button>
    <br />
    <button @click="undo()" class="icon">
      <span class="material-icons"> undo </span>
    </button>
    <br />
    <button @click="redo()" class="icon">
      <span class="material-icons"> redo </span>
    </button>
    <br />
    <Settings v-show="settingsShown" @close="settingsShown = false" />
    <Help
      v-show="helpShown"
      @close="helpShown = false"
      @inputString="toggleInput()"
    />
    <InputString v-show="inputShown" @close="inputShown = false" />
  </div>
</template>

<script>
import { sudokuHelpers } from "@/helpers";
import { mapActions, mapGetters } from "vuex";

import Settings from "./Settings.vue";
import Help from "./Help.vue";
import InputString from "./InputString.vue";

export default {
  name: "Controls",
  data: function () {
    return { settingsShown: false, helpShown: false, inputShown: false };
  },
  components: { Settings, Help, InputString },
  computed: { ...mapGetters("sudokuStrings", ["current"]) },
  methods: {
    ...mapActions("sudokuStrings", ["resetGridAction", "newGridAction"]),
    ...mapActions("root", ["showBackgroundAction"]),
    ...mapActions("clues", ["clearAllClues"]),
    ...mapActions(["undo", "redo"]),

    checkSudoku: function () {
      if (sudokuHelpers.validateSudokuString(this.current)) {
        alert("all good");
      } else {
        alert("there`s a booboo");
      }
    },

    toggleSettings: function () {
      this.settingsShown = !this.settingsShown;
      this.helpShown = false;
      this.inputShown = false;
      this.showBackgroundAction();
    },
    toggleHelp: function () {
      this.helpShown = !this.helpShown;
      this.settingsShown = false;
      this.inputShown = false;
      this.showBackgroundAction();
    },
    toggleInput: function () {
      this.inputShown = !this.inputShown;
      this.helpShown = false;
      this.settingsShown = false;
      this.showBackgroundAction();
    },

    resetGame: function () {
      if (confirm("Are you sure you want to reset the board?")) {
        this.resetGridAction();
        this.clearAllClues();
      }
    },

    newGame: function () {
      if (confirm("Are you sure you want to start a new game?")) {
        this.newGridAction();
        this.clearAllClues();
      }
    },
  },
};
</script>

<style>
.controls {
  margin-left: 20px;
  width: 150px;
  text-align: left;
  position: absolute;

  left: -webkit-calc(50% + 800px/2);
  left: -moz-calc(50% + 800px/2);
  left: calc(50% + 800px / 2);
  top: -webkit-calc(50% - 800px/2);
  top: -moz-calc(50% - 800px/2);
  top: calc(50% - 800px / 2);
}

button.icon {
  width: 60px;
  height: 60px;
  padding: 0;
}
button.icon span {
  color: #750b95;
  font-size: 40px;
  line-height: 55px;
}
button.icon.small {
  width: 30px;
  height: 30px;
}
button.icon.small span {
  font-size: 15px;
  line-height: 27px;
}
button.icon.selected span {
  color: #fff;
}

.primary {
  background-color: #750b95;
  color: #fff;
}
</style>
