import { constants } from "@/constants";

let mutations = {
  setInputMethodMutation(state, inputMethod) {
    state.inputMethod = inputMethod;
  },
};

let actions = {
  setInputMethodAction: function (context, inputMethod) {
    context.commit("setInputMethodMutation", inputMethod);
  },
  toggleInputMethodAction: function (context) {
    let prev = "";
    let next = "";
    Object.keys(constants.INPUT_METHODS).forEach((key) => {
      let value = constants.INPUT_METHODS[key];
      if (prev == context.state.inputMethod) next = value;
      prev = value;
    });
    if (next == "") {
      next = Object.values(constants.INPUT_METHODS)[0];
    }
    context.commit("setInputMethodMutation", next);
  },
};

let getters = {
  inputMethod: (state) => state.inputMethod,
};

const state = {
  namespaced: true,
  state: {
    inputMethod: constants.DEFAULT_INPUT_METHOD,
  },
  mutations,
  actions,
  getters,
};

export default state;
