<template>
  <li
    :class="classValue"
    @mousedown="handleClick(index, $event)"
    @mouseenter="handleDrag(index, $event)"
  >
    {{ value == "." ? "" : value }}
    <CornerClues v-show="value == '.'" ref="CornerClues" />
    <CenterClues v-show="value == '.'" ref="CenterClues" />
  </li>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import CornerClues from "./CornerClues.vue";
import CenterClues from "./CenterClues.vue";

export default {
  name: "Cell",
  components: { CornerClues, CenterClues },
  props: {
    index: Number,
    value: String,
    initialValue: String,
  },
  methods: {
    ...mapActions("root", [
      "setMousedownAction",
      "setAddingSelectionAction",
      "setSelectedIndexesAction",
      "updateSelectedIndexesAction",
    ]),
    handleClick: function (index, evt) {
      this.setMousedownAction(true);

      // clicking on already selected cell will start clearing the selection
      if (this.selectedIndexes.indexOf(index) !== -1) {
        this.setAddingSelectionAction(false);
        this.updateSelectedIndexesAction(index);
        return;
      }

      // if the ctrl key is pressed - add to selection
      if (evt.ctrlKey) {
        this.setAddingSelectionAction(true);
        this.updateSelectedIndexesAction(index);
        return;
      }

      // start new selection
      this.setAddingSelectionAction(true);
      this.setSelectedIndexesAction(index);
    },
    handleDrag: function (index) {
      if (this.mousedown) {
        this.updateSelectedIndexesAction(index);
      }
    },
  },
  computed: {
    ...mapGetters("highlight", [
      "selectedDigitIndexes",
      "selectedRowIndexes",
      "selectedColumnIndexes",
      "selectedBoxIndexes",
    ]),
    ...mapGetters("settings", ["hlRCB", "hlDigits"]),
    ...mapGetters("root", ["mousedown", "selectedIndexes", "activeIndex"]),
    classValue: function () {
      let className = "cell ";

      // handle active cell
      if (this.selectedIndexes && this.activeIndex == this.index)
        className += "active ";

      // handle selected cell
      if (this.selectedIndexes && this.selectedIndexes.includes(this.index))
        className += "selected ";

      // same digits highlight
      if (
        this.hlDigits &&
        this.selectedDigitIndexes &&
        this.selectedDigitIndexes.includes(this.index)
      )
        className += "sameDigit ";

      // handle selected row
      if (this.hlRCB && this.selectedRowIndexes.includes(this.index))
        className += "sameRow ";

      if (this.hlRCB && this.selectedColumnIndexes.includes(this.index))
        className += "sameColumn ";

      if (this.hlRCB && this.selectedBoxIndexes.includes(this.index)) {
        className += "sameBox ";
      }
      if (this.initialValue == ".") className += "customDigit ";

      return className;
    },
  },
};
</script>
<style lang="scss" scoped>
.cell {
  position: relative;
  color: #374d54;
  font-smooth: 2em;
}
</style>
